@media screen and (min-width: 768px){
    .hero, .experience, .skills, .projects, .contact {
        @apply h-screen grid grid-cols-2 md:place-content-center;
    }

    .left {
        @apply col-span-1 p-8 ml-16;
    }

    .right {
        @apply col-start-2 col-span-1 p-8 mr-16;
    }
}

@media screen and (max-width: 768px){
    .hero, .experience, .skills, .projects, .contact {
        @apply min-h-screen flex flex-col justify-center items-center;
    }

    .left, .right {
        @apply py-4 px-4;
    }
}

.left, .right {
    @apply backdrop-blur-sm bg-color-font/30 rounded-3xl text-white mx-2;
}